<template>
  <div class="audio-recorder-container">
    <div class="page-header">
      <h1>ğŸ¤ éŸ³é¢‘å½•éŸ³å™¨</h1>
      <p class="page-description">åœ¨çº¿å½•åˆ¶éŸ³é¢‘ï¼Œæ”¯æŒå®æ—¶æ³¢å½¢æ˜¾ç¤ºå’Œå¤šç§æ ¼å¼å¯¼å‡º</p>
    </div>

    <div class="recorder-section">
      <!-- å½•éŸ³çŠ¶æ€æ˜¾ç¤º -->
      <div class="recording-status">
        <div class="status-indicator" :class="{ 'recording': isRecording, 'paused': isPaused }">
          <div class="status-dot"></div>
          <span class="status-text">
            {{ isRecording ? (isPaused ? 'å·²æš‚åœ' : 'å½•åˆ¶ä¸­') : 'å‡†å¤‡å°±ç»ª' }}
          </span>
          <span v-if="isRecording || recordingTime > 0" class="recording-time">
            {{ formatTime(recordingTime) }}
          </span>
        </div>
      </div>

      <!-- æ³¢å½¢æ˜¾ç¤ºåŒºåŸŸ -->
      <div class="waveform-container">
        <canvas ref="waveformCanvas" class="waveform-canvas"></canvas>
        <div v-if="!microphonePermission" class="permission-placeholder">
          <div class="placeholder-icon">ğŸ¤</div>
          <div class="placeholder-text">éœ€è¦éº¦å…‹é£æƒé™æ‰èƒ½æ˜¾ç¤ºæ³¢å½¢</div>
        </div>
      </div>

      <!-- æ§åˆ¶æŒ‰é’® -->
      <div class="control-buttons">
        <div class="main-controls">
          <el-button
            v-if="!isRecording && !audioBlob"
            type="primary"
            size="large"
            @click="startRecording"
            :loading="startingRecording"
          >
            <i class="el-icon-microphone"></i> å¼€å§‹å½•éŸ³
          </el-button>

          <template v-else-if="isRecording">
            <el-button
              v-if="!isPaused"
              type="warning"
              size="large"
              @click="pauseRecording"
            >
              <i class="el-icon-video-pause"></i> æš‚åœ
            </el-button>
            
            <el-button
              v-else
              type="success"
              size="large"
              @click="resumeRecording"
            >
              <i class="el-icon-video-play"></i> ç»§ç»­
            </el-button>

            <el-button
              type="danger"
              size="large"
              @click="stopRecording"
            >
              <i class="el-icon-close"></i> åœæ­¢
            </el-button>
          </template>

          <template v-else-if="audioBlob">
            <el-button
              type="primary"
              size="large"
              @click="startRecording"
            >
              <i class="el-icon-refresh"></i> é‡æ–°å½•éŸ³
            </el-button>
          </template>
        </div>

        <!-- éŸ³é‡æ§åˆ¶ -->
        <div class="volume-control" v-if="microphonePermission">
          <span class="volume-label">éŸ³é‡:</span>
          <div class="volume-bar">
            <div class="volume-level" :style="{ width: volumeLevel + '%' }"></div>
          </div>
          <span class="volume-value">{{ Math.round(volumeLevel) }}%</span>
        </div>
      </div>

      <!-- å½•éŸ³ç»“æœ -->
      <div v-if="audioBlob" class="recording-result">
        <div class="result-header">
          <h3>å½•éŸ³å®Œæˆ</h3>
          <div class="recording-info">
            <span>æ—¶é•¿: {{ formatTime(recordingDuration) }}</span>
            <span>å¤§å°: {{ formatFileSize(audioBlob.size) }}</span>
          </div>
        </div>

        <!-- éŸ³é¢‘æ’­æ”¾å™¨ -->
        <div class="audio-player">
          <audio ref="audioPlayer" controls :src="audioUrl"></audio>
        </div>

        <!-- å¯¼å‡ºé€‰é¡¹ -->
        <div class="export-options">
          <h4>å¯¼å‡ºé€‰é¡¹</h4>
          <div class="format-selection">
            <el-radio-group v-model="exportFormat">
              <el-radio label="webm">WebM (åŸå§‹æ ¼å¼)</el-radio>
              <el-radio label="wav">WAV (æ— æŸ)</el-radio>
              <el-radio label="mp3">MP3 (å‹ç¼©)</el-radio>
            </el-radio-group>
          </div>

          <div class="export-buttons">
            <el-button type="success" @click="downloadRecording">
              <i class="el-icon-download"></i> ä¸‹è½½å½•éŸ³
            </el-button>
            <el-button @click="shareRecording" v-if="canShare">
              <i class="el-icon-share"></i> åˆ†äº«
            </el-button>
            <el-button type="danger" @click="deleteRecording">
              <i class="el-icon-delete"></i> åˆ é™¤
            </el-button>
          </div>
        </div>
      </div>

      <!-- å½•éŸ³å†å² -->
      <div v-if="recordingHistory.length > 0" class="recording-history">
        <h3>å½•éŸ³å†å²</h3>
        <div class="history-list">
          <div
            v-for="(record, index) in recordingHistory"
            :key="index"
            class="history-item"
          >
            <div class="history-info">
              <span class="history-name">å½•éŸ³ {{ index + 1 }}</span>
              <span class="history-time">{{ record.timestamp }}</span>
              <span class="history-duration">{{ formatTime(record.duration) }}</span>
            </div>
            <div class="history-actions">
              <el-button size="mini" @click="playHistoryRecord(record)">æ’­æ”¾</el-button>
              <el-button size="mini" type="success" @click="downloadHistoryRecord(record)">ä¸‹è½½</el-button>
              <el-button size="mini" type="danger" @click="deleteHistoryRecord(index)">åˆ é™¤</el-button>
            </div>
          </div>
        </div>
        <div class="history-actions">
          <el-button @click="clearHistory">æ¸…ç©ºå†å²</el-button>
        </div>
      </div>
    </div>

    <!-- è®¾ç½®é¢æ¿ -->
    <div class="settings-panel">
      <h3>å½•éŸ³è®¾ç½®</h3>
      <el-form label-width="120px">
        <el-form-item label="éŸ³é¢‘è´¨é‡">
          <el-select v-model="audioQuality" @change="updateAudioSettings">
            <el-option label="é«˜è´¨é‡ (48kHz)" value="high"></el-option>
            <el-option label="æ ‡å‡†è´¨é‡ (44.1kHz)" value="standard"></el-option>
            <el-option label="ä½è´¨é‡ (22kHz)" value="low"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="å£°é“">
          <el-radio-group v-model="audioChannels" @change="updateAudioSettings">
            <el-radio :label="1">å•å£°é“</el-radio>
            <el-radio :label="2">ç«‹ä½“å£°</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="è‡ªåŠ¨åœæ­¢">
          <el-switch v-model="autoStop"></el-switch>
          <span class="setting-desc">å½•éŸ³æ—¶é•¿è¾¾åˆ°é™åˆ¶æ—¶è‡ªåŠ¨åœæ­¢</span>
        </el-form-item>

        <el-form-item label="æœ€å¤§æ—¶é•¿" v-if="autoStop">
          <el-input-number
            v-model="maxDuration"
            :min="10"
            :max="3600"
            :step="10"
          ></el-input-number>
          <span class="setting-desc">ç§’</span>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AudioRecorderView',
  metaInfo: {
    title: 'éŸ³é¢‘å½•éŸ³å™¨ - åœ¨çº¿å½•éŸ³å·¥å…·',
    meta: [
      { name: 'description', content: 'åœ¨çº¿éŸ³é¢‘å½•éŸ³å™¨ï¼Œæ”¯æŒå®æ—¶æ³¢å½¢æ˜¾ç¤ºã€æš‚åœç»§ç»­å½•éŸ³ã€å¤šç§æ ¼å¼å¯¼å‡ºã€‚å¯å½•åˆ¶è¯­éŸ³å¤‡å¿˜ã€éŸ³ä¹ç‰‡æ®µã€ä¼šè®®è®°å½•ç­‰ï¼Œæ”¯æŒWAVã€MP3ã€WebMæ ¼å¼å¯¼å‡ºã€‚' },
      { name: 'keywords', content: 'éŸ³é¢‘å½•éŸ³å™¨,åœ¨çº¿å½•éŸ³,è¯­éŸ³å½•åˆ¶,å½•éŸ³å·¥å…·,éŸ³é¢‘å½•åˆ¶,éº¦å…‹é£å½•éŸ³,WAVå½•éŸ³,MP3å½•éŸ³,è¯­éŸ³å¤‡å¿˜,ä¼šè®®å½•éŸ³,éŸ³é¢‘ç¼–è¾‘' }
    ]
  },
  data() {
    return {
      // å½•éŸ³çŠ¶æ€
      isRecording: false,
      isPaused: false,
      startingRecording: false,
      recordingTime: 0,
      recordingDuration: 0,
      
      // åª’ä½“ç›¸å…³
      mediaRecorder: null,
      audioStream: null,
      audioBlob: null,
      audioUrl: null,
      microphonePermission: false,
      
      // æ³¢å½¢æ˜¾ç¤º
      audioContext: null,
      analyser: null,
      dataArray: null,
      animationId: null,
      volumeLevel: 0,
      
      // å½•éŸ³è®¾ç½®
      audioQuality: 'standard',
      audioChannels: 1,
      exportFormat: 'webm',
      autoStop: false,
      maxDuration: 300,
      
      // å½•éŸ³å†å²
      recordingHistory: [],
      
      // å…¶ä»–
      recordingTimer: null,
      canShare: 'share' in navigator,
      audioChunks: []
    }
  },
  mounted() {
    this.loadRecordingHistory()
    this.checkMicrophonePermission()
  },
  beforeDestroy() {
    this.cleanup()
  },
  methods: {
    // æ£€æŸ¥éº¦å…‹é£æƒé™
    async checkMicrophonePermission() {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({ audio: true })
        this.microphonePermission = true
        stream.getTracks().forEach(track => track.stop())
      } catch (error) {
        this.microphonePermission = false
        console.warn('éº¦å…‹é£æƒé™è¢«æ‹’ç»:', error)
      }
    },

    // å¼€å§‹å½•éŸ³
    async startRecording() {
      try {
        this.startingRecording = true
        
        // è·å–éŸ³é¢‘æµ
        const constraints = this.getAudioConstraints()
        this.audioStream = await navigator.mediaDevices.getUserMedia(constraints)
        
        // è®¾ç½®éŸ³é¢‘ä¸Šä¸‹æ–‡å’Œåˆ†æå™¨
        this.setupAudioAnalyzer()
        
        // åˆ›å»ºåª’ä½“å½•åˆ¶å™¨
        this.mediaRecorder = new MediaRecorder(this.audioStream, {
          mimeType: this.getSupportedMimeType()
        })
        
        this.audioChunks = []
        
        this.mediaRecorder.ondataavailable = (event) => {
          if (event.data.size > 0) {
            this.audioChunks.push(event.data)
          }
        }
        
        this.mediaRecorder.onstop = () => {
          this.handleRecordingStop()
        }
        
        // å¼€å§‹å½•éŸ³
        this.mediaRecorder.start(100) // æ¯100msæ”¶é›†ä¸€æ¬¡æ•°æ®
        this.isRecording = true
        this.isPaused = false
        this.recordingTime = 0
        this.startRecordingTimer()
        this.startWaveformAnimation()
        
        // æ¸…é™¤ä¹‹å‰çš„å½•éŸ³
        if (this.audioBlob) {
          URL.revokeObjectURL(this.audioUrl)
          this.audioBlob = null
          this.audioUrl = null
        }
        
        this.$message.success('å¼€å§‹å½•éŸ³')
        
      } catch (error) {
        console.error('å¯åŠ¨å½•éŸ³å¤±è´¥:', error)
        this.$message.error('å¯åŠ¨å½•éŸ³å¤±è´¥: ' + error.message)
      } finally {
        this.startingRecording = false
      }
    },

    // æš‚åœå½•éŸ³
    pauseRecording() {
      if (this.mediaRecorder && this.mediaRecorder.state === 'recording') {
        this.mediaRecorder.pause()
        this.isPaused = true
        this.stopRecordingTimer()
        this.$message.info('å½•éŸ³å·²æš‚åœ')
      }
    },

    // ç»§ç»­å½•éŸ³
    resumeRecording() {
      if (this.mediaRecorder && this.mediaRecorder.state === 'paused') {
        this.mediaRecorder.resume()
        this.isPaused = false
        this.startRecordingTimer()
        this.$message.info('ç»§ç»­å½•éŸ³')
      }
    },

    // åœæ­¢å½•éŸ³
    stopRecording() {
      if (this.mediaRecorder && this.mediaRecorder.state !== 'inactive') {
        this.mediaRecorder.stop()
        this.isRecording = false
        this.isPaused = false
        this.stopRecordingTimer()
        this.stopWaveformAnimation()
        this.recordingDuration = this.recordingTime
        this.$message.success('å½•éŸ³å·²åœæ­¢')
      }
    },

    // å¤„ç†å½•éŸ³åœæ­¢
    handleRecordingStop() {
      this.audioBlob = new Blob(this.audioChunks, { type: this.getSupportedMimeType() })
      this.audioUrl = URL.createObjectURL(this.audioBlob)

      // ä¿å­˜åˆ°å†å²è®°å½•
      this.saveToHistory()

      // æ¸…ç†èµ„æº
      this.cleanup()
    },

    // è·å–éŸ³é¢‘çº¦æŸ
    getAudioConstraints() {
      const qualitySettings = {
        high: { sampleRate: 48000, channelCount: this.audioChannels },
        standard: { sampleRate: 44100, channelCount: this.audioChannels },
        low: { sampleRate: 22050, channelCount: this.audioChannels }
      }

      return {
        audio: {
          ...qualitySettings[this.audioQuality],
          echoCancellation: true,
          noiseSuppression: true,
          autoGainControl: true
        }
      }
    },

    // è·å–æ”¯æŒçš„MIMEç±»å‹
    getSupportedMimeType() {
      const types = [
        'audio/webm;codecs=opus',
        'audio/webm',
        'audio/mp4',
        'audio/wav'
      ]

      for (const type of types) {
        if (MediaRecorder.isTypeSupported(type)) {
          return type
        }
      }

      return 'audio/webm'
    },

    // è®¾ç½®éŸ³é¢‘åˆ†æå™¨
    setupAudioAnalyzer() {
      try {
        this.audioContext = new (window.AudioContext || window.webkitAudioContext)()
        this.analyser = this.audioContext.createAnalyser()

        const source = this.audioContext.createMediaStreamSource(this.audioStream)
        source.connect(this.analyser)

        this.analyser.fftSize = 256
        const bufferLength = this.analyser.frequencyBinCount
        this.dataArray = new Uint8Array(bufferLength)
      } catch (error) {
        console.warn('éŸ³é¢‘åˆ†æå™¨è®¾ç½®å¤±è´¥:', error)
      }
    },

    // å¼€å§‹æ³¢å½¢åŠ¨ç”»
    startWaveformAnimation() {
      const canvas = this.$refs.waveformCanvas
      if (!canvas || !this.analyser) return

      const ctx = canvas.getContext('2d')
      const width = canvas.width = canvas.offsetWidth
      const height = canvas.height = canvas.offsetHeight

      const animate = () => {
        if (!this.isRecording || this.isPaused) return

        this.analyser.getByteFrequencyData(this.dataArray)

        // è®¡ç®—éŸ³é‡çº§åˆ«
        let sum = 0
        for (let i = 0; i < this.dataArray.length; i++) {
          sum += this.dataArray[i]
        }
        this.volumeLevel = (sum / this.dataArray.length / 255) * 100

        // ç»˜åˆ¶æ³¢å½¢
        ctx.fillStyle = '#1a1a1a'
        ctx.fillRect(0, 0, width, height)

        const barWidth = width / this.dataArray.length * 2.5
        let barHeight
        let x = 0

        for (let i = 0; i < this.dataArray.length; i++) {
          barHeight = (this.dataArray[i] / 255) * height

          const gradient = ctx.createLinearGradient(0, height - barHeight, 0, height)
          gradient.addColorStop(0, '#409eff')
          gradient.addColorStop(1, '#67c23a')

          ctx.fillStyle = gradient
          ctx.fillRect(x, height - barHeight, barWidth, barHeight)

          x += barWidth + 1
        }

        this.animationId = requestAnimationFrame(animate)
      }

      animate()
    },

    // åœæ­¢æ³¢å½¢åŠ¨ç”»
    stopWaveformAnimation() {
      if (this.animationId) {
        cancelAnimationFrame(this.animationId)
        this.animationId = null
      }

      // æ¸…ç©ºç”»å¸ƒ
      const canvas = this.$refs.waveformCanvas
      if (canvas) {
        const ctx = canvas.getContext('2d')
        ctx.clearRect(0, 0, canvas.width, canvas.height)
      }

      this.volumeLevel = 0
    },

    // å¼€å§‹å½•éŸ³è®¡æ—¶å™¨
    startRecordingTimer() {
      this.recordingTimer = setInterval(() => {
        this.recordingTime++

        // è‡ªåŠ¨åœæ­¢æ£€æŸ¥
        if (this.autoStop && this.recordingTime >= this.maxDuration) {
          this.stopRecording()
          this.$message.warning(`å½•éŸ³æ—¶é•¿å·²è¾¾åˆ° ${this.maxDuration} ç§’ï¼Œè‡ªåŠ¨åœæ­¢`)
        }
      }, 1000)
    },

    // åœæ­¢å½•éŸ³è®¡æ—¶å™¨
    stopRecordingTimer() {
      if (this.recordingTimer) {
        clearInterval(this.recordingTimer)
        this.recordingTimer = null
      }
    },

    // æ ¼å¼åŒ–æ—¶é—´
    formatTime(seconds) {
      const mins = Math.floor(seconds / 60)
      const secs = seconds % 60
      return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
    },

    // æ ¼å¼åŒ–æ–‡ä»¶å¤§å°
    formatFileSize(bytes) {
      if (bytes === 0) return '0 B'
      const k = 1024
      const sizes = ['B', 'KB', 'MB', 'GB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    },

    // ä¸‹è½½å½•éŸ³
    downloadRecording() {
      if (!this.audioBlob) return

      const timestamp = new Date().toISOString().replace(/[:.]/g, '-')
      const extension = this.getFileExtension()
      const filename = `å½•éŸ³_${timestamp}.${extension}`

      const url = URL.createObjectURL(this.audioBlob)
      const a = document.createElement('a')
      a.href = url
      a.download = filename
      document.body.appendChild(a)
      a.click()
      document.body.removeChild(a)
      URL.revokeObjectURL(url)

      this.$message.success('å½•éŸ³ä¸‹è½½æˆåŠŸ')
    },

    // åˆ†äº«å½•éŸ³
    async shareRecording() {
      if (!this.audioBlob || !this.canShare) return

      try {
        const file = new File([this.audioBlob], 'å½•éŸ³.webm', { type: this.audioBlob.type })
        await navigator.share({
          title: 'å½•éŸ³åˆ†äº«',
          text: 'æˆ‘å½•åˆ¶äº†ä¸€æ®µéŸ³é¢‘',
          files: [file]
        })
        this.$message.success('åˆ†äº«æˆåŠŸ')
      } catch (error) {
        console.error('åˆ†äº«å¤±è´¥:', error)
        this.$message.error('åˆ†äº«å¤±è´¥')
      }
    },

    // åˆ é™¤å½•éŸ³
    deleteRecording() {
      this.$confirm('ç¡®å®šè¦åˆ é™¤å½“å‰å½•éŸ³å—ï¼Ÿ', 'ç¡®è®¤åˆ é™¤', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        if (this.audioUrl) {
          URL.revokeObjectURL(this.audioUrl)
        }
        this.audioBlob = null
        this.audioUrl = null
        this.recordingDuration = 0
        this.$message.success('å½•éŸ³å·²åˆ é™¤')
      }).catch(() => {})
    },

    // è·å–æ–‡ä»¶æ‰©å±•å
    getFileExtension() {
      const formatMap = {
        webm: 'webm',
        wav: 'wav',
        mp3: 'mp3'
      }
      return formatMap[this.exportFormat] || 'webm'
    },

    // ä¿å­˜åˆ°å†å²è®°å½•
    saveToHistory() {
      const record = {
        blob: this.audioBlob,
        url: this.audioUrl,
        duration: this.recordingDuration,
        timestamp: new Date().toLocaleString(),
        size: this.audioBlob.size
      }

      this.recordingHistory.unshift(record)

      // é™åˆ¶å†å²è®°å½•æ•°é‡
      if (this.recordingHistory.length > 10) {
        const removed = this.recordingHistory.pop()
        if (removed.url) {
          URL.revokeObjectURL(removed.url)
        }
      }

      this.saveRecordingHistory()
    },

    // æ’­æ”¾å†å²å½•éŸ³
    playHistoryRecord(record) {
      const audio = new Audio(record.url)
      audio.play()
    },

    // ä¸‹è½½å†å²å½•éŸ³
    downloadHistoryRecord(record) {
      const timestamp = record.timestamp.replace(/[^\d]/g, '')
      const filename = `å†å²å½•éŸ³_${timestamp}.webm`

      const a = document.createElement('a')
      a.href = record.url
      a.download = filename
      document.body.appendChild(a)
      a.click()
      document.body.removeChild(a)

      this.$message.success('å†å²å½•éŸ³ä¸‹è½½æˆåŠŸ')
    },

    // åˆ é™¤å†å²å½•éŸ³
    deleteHistoryRecord(index) {
      this.$confirm('ç¡®å®šè¦åˆ é™¤è¿™æ¡å†å²å½•éŸ³å—ï¼Ÿ', 'ç¡®è®¤åˆ é™¤', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        const record = this.recordingHistory[index]
        if (record.url) {
          URL.revokeObjectURL(record.url)
        }
        this.recordingHistory.splice(index, 1)
        this.saveRecordingHistory()
        this.$message.success('å†å²å½•éŸ³å·²åˆ é™¤')
      }).catch(() => {})
    },

    // æ¸…ç©ºå†å²è®°å½•
    clearHistory() {
      this.$confirm('ç¡®å®šè¦æ¸…ç©ºæ‰€æœ‰å†å²å½•éŸ³å—ï¼Ÿ', 'ç¡®è®¤æ¸…ç©º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        this.recordingHistory.forEach(record => {
          if (record.url) {
            URL.revokeObjectURL(record.url)
          }
        })
        this.recordingHistory = []
        this.saveRecordingHistory()
        this.$message.success('å†å²è®°å½•å·²æ¸…ç©º')
      }).catch(() => {})
    },

    // æ›´æ–°éŸ³é¢‘è®¾ç½®
    updateAudioSettings() {
      // å¦‚æœæ­£åœ¨å½•éŸ³ï¼Œæç¤ºç”¨æˆ·é‡æ–°å¼€å§‹
      if (this.isRecording) {
        this.$message.info('è®¾ç½®å°†åœ¨ä¸‹æ¬¡å½•éŸ³æ—¶ç”Ÿæ•ˆ')
      }
    },

    // ä¿å­˜å½•éŸ³å†å²åˆ°æœ¬åœ°å­˜å‚¨
    saveRecordingHistory() {
      try {
        // åªä¿å­˜åŸºæœ¬ä¿¡æ¯ï¼Œä¸ä¿å­˜blobæ•°æ®
        const historyData = this.recordingHistory.map(record => ({
          duration: record.duration,
          timestamp: record.timestamp,
          size: record.size
        }))
        localStorage.setItem('audioRecorderHistory', JSON.stringify(historyData))
      } catch (error) {
        console.warn('ä¿å­˜å½•éŸ³å†å²å¤±è´¥:', error)
      }
    },

    // åŠ è½½å½•éŸ³å†å²
    loadRecordingHistory() {
      try {
        const historyData = localStorage.getItem('audioRecorderHistory')
        if (historyData) {
          // åªåŠ è½½åŸºæœ¬ä¿¡æ¯ï¼Œå®é™…çš„éŸ³é¢‘æ•°æ®åœ¨å½•éŸ³æ—¶ç”Ÿæˆ
          // æ¸…ç©ºå†å²è®°å½•ï¼Œå› ä¸ºéŸ³é¢‘æ•°æ®æ— æ³•æŒä¹…åŒ–
          this.recordingHistory = []
        }
      } catch (error) {
        console.warn('åŠ è½½å½•éŸ³å†å²å¤±è´¥:', error)
      }
    },

    // æ¸…ç†èµ„æº
    cleanup() {
      if (this.audioStream) {
        this.audioStream.getTracks().forEach(track => track.stop())
        this.audioStream = null
      }

      if (this.audioContext) {
        this.audioContext.close()
        this.audioContext = null
      }

      this.stopRecordingTimer()
      this.stopWaveformAnimation()
    }
  }
}
